import React, { useEffect, useState } from 'react'
import { urlFor, client } from '../../client'
import { runFireworks } from '../../lib/utils'

import Top3 from './Top3'

import './WinnerResults.css'



const WinnerResults = (props) => {


    const [members, setSkills] = useState([]);

    useEffect(() => {
        const skillsQuery = '*[_type == "skills"]';

        client.fetch(skillsQuery)
            .then((data) => {
                setSkills(data);
            })
    }, [])

    const testLIst = [
        {
            name: 'bob',
            changeResult: 3.0
        },
        {
            name: 'Sherry',
            changeResult: 7.0
        },
        {
            name: 'Tom',
            changeResult: 2.02
        },
        {
            name: 'Ed',
            changeResult: 2.01
        },
        {
            name: 'Smith',
            changeResult: 2.69
        },
    ]

    runFireworks();

    console.log(props)

    return (
        <div className='winnerResults-box'>
            <button className='winnerResults-close-btn' onClick={() => props.setState(false)}>X</button>
            <h1>Week 1 Results</h1>
            <div className='winnerResults-top-3-wrapper'>
                <Top3 list={members} />
            </div>
        </div>
    )
}

export default WinnerResults