import React from 'react'
import { urlFor, client } from '../../client'
import { motion } from 'framer-motion';
import CountUp from 'react-countup';

import './WinnerResults.css';

const Top3 = ({ list }) => {


    const itemVariants = {
        initial: { x: "-100vw", opacity: 0 },
        animate: { x: 0, opacity: 1 },
    };

    const checkPlacement = (index) => {
        switch (index) {
            case 0:
                return 'top-3-1';

            case 1:
                return 'top-3-2';

            case 2:
                return 'top-3-3';

            default:
                return 'display-none';
                break;
        }
    }

    const checkPlacementNo = (index) => {
        switch (index) {
            case 0:
                return '1st';

            case 1:
                return '2nd';

            case 2:
                return '3rd';

            default:
                return 'display-none';
                break;
        }
    }

    return (
        <>
            {/* <div className='top-3-1'>
                <img src='https://via.placeholder.com/150' />
            </div>
            <div className='top-3-2'>
                <img src='https://via.placeholder.com/150' />
            </div>
            <div className='top-3-3'>
                <img src='https://via.placeholder.com/150' />
            </div> */}


            {list.sort(function (a, b) {
                return a.changeResult - b.changeResult;
            }).reverse().map((m, index) => (
                <motion.div
                    className={checkPlacement(index)}
                    initial={{ opacity: 0, scale: 0.5 }}
                    animate={{ opacity: 1, scale: [1, 1.5, 1] }}
                    transition={{ duration: 0.3, delay: index * 0.8 }}>
                    {index === 0 && <p className='crown'></p>}
                    <img src={(m.pic) ? urlFor(m.pic) : "https://via.placeholder.com/150"} />
                    <h2>{m.name}</h2>
                    <h3>{checkPlacementNo(index)}</h3>
                    <p><CountUp end={m.changeResult} start={(index === 0) ? (index + 1) * 100 : index * 100} duration={(index === 0) ? (index + 1) * 1.5 : index * 1.5} decimals={2} />%</p>
                </motion.div>
            ))}

        </>

    )
}

export default Top3

