import React, { useState } from 'react';
import { motion } from "framer-motion";
import { Header, ScoreboardWrapper, Footer } from './containers'

import images from './assets/images'
import './App.css'
import { SideMenu, WinnerResults } from './components';

function App() {

    const [resultsModal, setResultsModal] = useState(true)

    // const showResults = (bool) => {
    //     setResultsModal(bool)
    // }

    return (
        <>
            {resultsModal &&
                <WinnerResults setState={setResultsModal} />
            }
            {resultsModal && <div className='App-dim' />}
            <SideMenu />

            <div className="App">

                <Header />
                <ScoreboardWrapper />
                <Footer />
                <motion.button
                    className={resultsModal ? "results-week-1-hide" : "results-week-1"}
                    onClick={() => setResultsModal(!resultsModal)}
                    whileHover={{ scale: 1.2 }}
                    whileTap={{ scale: 0.9 }}
                    transition={{ type: "spring", stiffness: 400, damping: 17 }}
                >
                    {resultsModal ? "Hide" : "Week 1 Results"}
                </motion.button>
            </div>
        </>
    );
}

export default App;
