import React, { useState, useEffect } from 'react'
import { client } from '../../client'
import CountUp from 'react-countup';
import images from '../../assets/images';
import './PotCount.css'

const PotCount = () => {


    const [potCountNumber, setPotCountNumber] = useState([]);

    useEffect(() => {
        const potCountNumberQuery = '*[_type == "tracking"]';

        client.fetch(potCountNumberQuery)
            .then((data) => {
                setPotCountNumber(data);
            })
    }, [])

    // console.log(potCountNumber[0])

    return (
        <div className='potCount-container'>
            <img src={images.potImg} alt="prize-pot" />
            <h1>$<CountUp end={330} /></h1>
        </div>
    )
}

export default PotCount